/* Remove Gap Between Banner and Top */
body,
html {
  margin: 0;
  padding: 0;
}
#app {
  margin: 0 auto;
  padding: 0;
  font-weight: normal;
}
.top-banner {
  background-color: #007bff; /* Blue background */
  color: white;
  padding: 10px 0; /* Thinner padding */
  position: relative;
  top: 0;
  width: 100%; /* Full width */
}
/* From Uiverse.io by ernestnash */
.btn {
 display: inline-block;
 padding: 0.9rem 1.8rem;
 font-size: 16px;
 font-weight: 700;
 color: white;
 border: 3px solid rgb(0, 26, 255);
 cursor: pointer;
 position: relative;
 background-color: transparent;
 color: white;
 text-decoration: none;
 overflow: hidden;
 z-index: 1;
 font-family: inherit;
}
.btn::before {
 content: "";
 position: absolute;
 left: 0;
 top: 0;
 width: 100%;
 height: 100%;
 background-color: rgb(0, 26, 255);
 transform: translateX(-105%);
 transition: all .3s;
 z-index: -1;
}
.btn:hover::before {
 transform: translateX(0);
}
.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.btn:hover::before {
 transform: translateX(0);
}
.bank-title {
  margin: 0;
  font-size: 1.5rem;
}
.auth-buttons {
  display: flex;
  gap: 10px;
}
.auth-buttons .btn {
  font-size: 0.9rem;
  padding: 5px 15px;
}

/* Background Image */
.background-section {
  position: relative;
  width: 100%;
  height: 400px; /* Adjust height as needed */
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}); /* Set your image path */
  background-size: cover; /* Ensures the image covers the section */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Prevent tiling */
}


/* Info Sections */
.info-sections {
  padding: 40px 20px;
}
.info-sections .col-md-4 {
  margin-bottom: 20px;
}
.info-sections h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}
.info-sections p {
  font-size: 1rem;
  color: #555;
}

/* Footer */
footer {
  margin-top: 20px;
  padding: 20px 0;
  background-color: #f8f9fa;
  color: #6c757d;
}
